import { Component, getAssetPath, h, Prop } from '@stencil/core';

@Component({
  tag: 'dc-ui-logo',
  styleUrl: 'dc-ui-logo.scss',
  shadow: true,
  assetsDirs: ['../assets'],
})
export class DcUiLogo {
  @Prop() href?: string;

  render() {
    const logo = getAssetPath('../assets/rexroth_logo_animated.gif');
    const img = <img src={logo} />;

    return this.href ? <a href={this.href}>{img}</a> : img;
  }
}
